import React from "react";
import Link from "next/link";
import { LayoutDashboard, ShoppingBag, FileText, Settings, ShieldAlert, LogOut } from "lucide-react";

export default function AdminLayout({
    children,
}: {
    children: React.ReactNode;
}) {
    const navItems = [
        { name: "Overview", href: "/admin", icon: <LayoutDashboard className="w-5 h-5" /> },
        { name: "Products", href: "/admin/products", icon: <ShoppingBag className="w-5 h-5" /> },
        { name: "Pages", href: "/admin/pages", icon: <FileText className="w-5 h-5" /> },
        { name: "Logs & Security", href: "/admin/logs", icon: <ShieldAlert className="w-5 h-5" /> },
        { name: "Settings", href: "/admin/settings", icon: <Settings className="w-5 h-5" /> },
    ];

    return (
        <div className="min-h-screen bg-surface-elevated flex">
            {/* Sidebar */}
            <aside className="w-64 bg-white border-r border-border fixed h-full hidden md:flex flex-col">
                <div className="h-20 flex items-center px-6 border-b border-border">
                    <Link href="/" className="font-display font-bold text-xl text-primary">
                        intru.in <span className="text-text-tertiary text-sm font-sans">Admin</span>
                    </Link>
                </div>

                <nav className="flex-1 p-4 space-y-1">
                    {navItems.map((item) => (
                        <Link
                            key={item.name}
                            href={item.href}
                            className="flex items-center gap-3 px-4 py-3 text-sm font-medium text-text-secondary rounded-lg hover:bg-surface-elevated hover:text-primary transition-colors"
                        >
                            {item.icon}
                            {item.name}
                        </Link>
                    ))}
                </nav>

                <div className="p-4 border-t border-border">
                    <Link href="/" className="flex items-center gap-3 px-4 py-3 text-sm font-medium text-text-secondary rounded-lg hover:bg-surface-elevated transition-colors">
                        <LogOut className="w-5 h-5" />
                        Exit Admin
                    </Link>
                </div>
            </aside>

            {/* Main Content */}
            <main className="flex-1 md:ml-64 p-8">
                {children}
            </main>
        </div>
    );
}
